/* Header
----------------------------------------------------------- */
.header-main {
    @include size(100%, 150px);
    position: relative;
    max-width: 1440px;
    background: url(/img/bg-image.png) no-repeat;
}

.top-logo {
    display: inline-block;
    padding: 25px 45px;

    &_lk {
        font-size: $ft-size-xlarge;
        font-weight: 700;
        color: $white;
    }
}

.header-bottom {
    height: 50px;
    margin-top: 2px;
    padding: 0 45px;

    .it { /*input*/
        font-size: $ft-size-large;
        font-weight: 300;
        background-color: rgba(65, 47, 50, 0.7);
        border-color: transparent;
    }
}

[type="submit"] {
    border: none;
    outline: none;
    background-color: transparent;
}

.bottom-logo,
.search-form,
.btn-create,
.user-panel {
    @include inline-block;
}

.bottom-logo {
    margin-top: 10px;
    margin-right: 20px;

    &_lk {
        display: block;
        @include size(160px, 29px);

    }

    i {
        display: block;
        width: 160px;
        height: 29px;
        background: url(/img/sprite.png) -2px -0px no-repeat;
    }
}

.search-form {
    width: 100%;
    max-width: 280px;
    margin-top: 6px;
    margin-left: 20px;
}

.btn-create { /*button create*/
    margin-top: 6px;
    margin-left: 180px;

    [type="submit"] {
        @include size(40px);
        background: url(/img/sprite.png) -84px -31px no-repeat;
    }
}

.user-panel {
    float: right;
    margin-top: 5px;
}

.user-panel_messages {
    @include inline-block;
    position: relative;
    margin-right: 25px;

    &_icon {
        @include size(23px, 28px);
        display: block;
        background: url(/img/sprite.png) -82px -115px no-repeat;
    }

    &_figure {
        @include round-background();
        background-color: $red;
        padding: 4px 7px;
        font-size: $ft-size-small;
        color: $white;
        position: absolute;
        bottom: 32px;
        left: 25px;
    }
}

.user-panel_info {
    @include inline-block;
    margin-left: 25px;

    &_name {
        @include inline-block;
        color: $white;
        font-size: $ft-size-medium;
        margin-top: 13px;
    }

    &_avatar {
        @include inline-block;
        @include size(40px);
        background: url(/img/sprite.png) -84px -72px no-repeat;
        margin-left: 10px;
    }

    &_toggle {
        @include inline-block;
        @include size(18px, 9px);
        margin-top: 15px;
        background: url(/img/sprite.png) -132px -134px no-repeat;
    }
}

/* /Header
----------------------------------------------------------- */