/* Sidebar
----------------------------------------------------------- */
.nav-menu {
    width: 250px;
    align-self: stretch;
    padding-left: 40px;
    padding-top: 10px;
    background: $bg-nav-menu;
}

.switch-close {
    @include size(17px,15px);
    display: block;
    margin-left: auto;
    margin-right: 15px;

    &:before {
        @include size(15px, 14px);
        content: "";
        display: block;
        background: url("/img/sprite.png") -107px -115px;
    }
}

.menu-list {
    list-style: none;

    &_i { /*menu-list_item*/
        margin-top: 20px;
    }

    &_i:hover {
        background: #2c536d;
    }

    &_i.__active &_i_lk { /*menu-list_item__active menu-list_item_link*/
        color: $blue-1;
        background: $white;
    }

    &_i_lk { /*menu-list_item_link*/
        display: block;
        padding: 10px;
        font-size: $ft-size-large;
        color: $ft-color-lk-nav-menu;
    }

    &_i_lk:before { /*menu-list_item_link*/
        vertical-align: bottom;
        margin-right: 20px;
    }
}

.ic { /*icon with link */
    &-home:before {
        @include size(23px, 26px);
        @include icon-before(-107px, -134px);
    }

    &-people:before {
        @include size(25px, 28px);
        @include icon-before(-2px, -113px);
    }

    &-company:before {
        @include size(26px, 27px);
        @include icon-before(-126px, -105px);
    }

    &-message:before {
        @include size(27px, 19px);
        @include icon-before(-126px, -60px);
    }

    &-scheduling:before {
        @include size(27px, 22px);
        @include icon-before(-126px, -81px);
    }

    &-user:before {
        @include size(17px, 25px);
        @include icon-before(-56px, -139px);
    }

    &-support:before {
        @include size(27px);
        @include icon-before(-126px, -31px);
    }
}
/* /Sidebar
----------------------------------------------------------- */